<template>
  <div class="">
    <div class="row">
      <div class="col-lg-4 form-group">
        <label for="">IDCO</label>
        <input
          type="text"
          class="form-control"
          v-model="idco"
          placeholder="inserisci IDCO"
        />
      </div>
      <div class="col-lg-4 form-group">
        <label for="">Modello</label>
        <input
          type="text"
          class="form-control"
          v-model="model"
          placeholder="inserisci Modello"
        />
      </div>
      <div class="col-lg-4 form-group">
        <label for="">PDV</label>
        <input
          type="text"
          class="form-control"
          v-model="pdv"
          placeholder="inserisci pdv.zip"
        />
      </div>
      <div class="col-lg-4">
        <button class="btn btn-success" @click="submit">Cerca</button>
      </div>
    </div>
    <hr />
    <div class="row" v-if="data == 'ok'">
      <div
        class="col-sm-12 col-md-10 iframe-container"
        style="white-space: pre-line"
      >
        <iframe :src="url" frameborder="0"> </iframe>
      </div>
    </div>
    <div class="row" v-else>
      <div class="col-sm-12 col-md-10">{{ data }}</div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      idco: null,
      model: null,
      pdv: null,
      //   idco: 1154,
      //   model: "NotificaFatturaPA",
      //   pdv: "IdV_20201214221410.zip",
      url:
        "https://archicons.archivium-srl.it/ArchiHub/procedures/varie/retention/api.php?type=getTxtStream&idco=",
      data: "",
    };
  },
  methods: {
    submit() {
      if (this.idco == null || this.model == null || this.pdv == null) {
        this.data = "some data is null, please insert all field";
      } else {
        this.url += this.idco + "&model=" + this.model + "&pdv=" + this.pdv;
        this.data = "ok";
      }
    },
  },
};
</script>
<style scoped>
.iframe-container {
  overflow: hidden;
  /* 16:9 aspect ratio */
  padding-top: 56.25%;
  position: relative;
}
iframe {
  border: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
</style>